@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.AddUser_form__dCSb7 {
  margin-top: 10px;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(214, 217, 53);
  border-radius: 10px;
}
.AddUser_form__dCSb7 form {
  display: flex;
  flex-direction: column;
}
.AddUser_form__dCSb7 div {
  margin: 20px;
}
.AddUser_form__dCSb7 input,
.AddUser_form__dCSb7 label {
  width: 70%;
  height: 30px;
  display: block;
  border-radius: 5px;
  border: none;
  padding-left: 10px;
}

.AddUser_form__dCSb7 button {
  background-color: greenyellow;
  height: 50px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
}

.Users_users__dHdvn {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding: 10px;
  background-color: rgb(214, 217, 53);
  border-radius: 15px;
}

.Users_users__dHdvn button {
  background-color: greenyellow;
  height: 50px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
}

.Users_users__dHdvn div:nth-child(2) div {
  width: 80%;
  background-color: rgba(248, 121, 52, 0.713);
  border-radius: 10px;
}
.Users_users__dHdvn h3 {
  background-color: rgb(38, 255, 0);
  padding-left: 10px;
  border-radius: 10px;
}

.Users_users__dHdvn ul {
  list-style: decimal;
}

